









import { Component, Mixins, Prop, Watch } from "vue-property-decorator";
import TwoWay from "@/resources/mixins/two-way";

@Component({
    name: "MyImage"
})
export default class MyImage extends Mixins(TwoWay) {
    @Prop({ default: "edit" })
    mode!: string;
    // 点击预览
    clickHandler() {
        this.$nextTick(() => {
            (this.$refs["el-image"] as any).clickHandler();
        });
    }
}
